@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
 *{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
 }
 body{
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
 }
 #root{
  width: 100%;
  padding: 0 0.6rem;
 }
 .login-container{
  max-width: 410px;
  margin: 0 auto;
  border-radius: 0.5rem;
  padding: 2rem 1.5rem;
  background: #ffffff;
  box-shadow: 0 10ox 20px rgba(0, 0, 0, 1);
 }
 .login-container .form-title{
    text-align: center;
    font-size: 1.37rem;
    font-weight: 600;
    margin-bottom: 1.87rem;
 }

 .social-login .social-button{
  display: flex;
  width: 100%;
  gap: 0.81rem;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.75rem 0;
  align-items: center;
  border-radius: 0.31rem;
  justify-content: center;
  background: #dedee4;
  border: 1px solid #d5cbff;
  transition: 0.3s ease;
 }
 .social-login .social-button:hover{
    background: #9a98a0;
    border-color: #5f41;
 }
 .social-login .social-button .social-icon{
  width: 23px;
 }
 .login-container .separator{
margin: 1.5rem 0;
text-align: center;
position: relative;
 }
 .login-container .separator span{
    position: relative;
    z-index: 1;
    background: #fff;
    padding: 0 0.9rem;
    font-size: 1.06rem;
    font-weight: 500;
 }
 .login-container .separator::after{
    position: absolute;
    content: "";
    top: 50%;
    left: 0;
    width: 100%;
    height: 1px;
    background: #bfb3f2;
 }
 .login-form .input-wrapper{
    height: 54px;
    width: 100%;
    position: relative;
    margin-bottom: 1.5rem;
 }
 .login-form .input-wrapper .input-field{
    height: 100%;
    width: 100%;
    outline: none;
    font-size: 1.06rem;
    border-radius: 0.31rem;
    border: 1px solid #bfb3f2;
    padding: 0 1.25rem 0 3.12rem;
    transition: 0.2s ease;
 }
 .login-form .input-wrapper .input-field:focus{
   border-color: #5f41e4;
 }
 .login-form .input-wrapper .input-field::placeholder{
   color: #a395e0;
 }
 .login-form .input-wrapper i{
   color: #a395e0;
   position: absolute;
   top: 50%;
   transform: translateY(-50%);
   transition: 0.2s ease;
 }
 .login-form .input-wrapper i:not(.eye-icon){
   left: 0.9rem;
   pointer-events: none;
 }
 .login-form .input-wrapper .input-field:focus ~ i:not(.eye-icon){
   color: #5f41e4;
 }
 
 .login-form .input-wrapper i.eye-icon{
   font-size: 1.25rem;
   cursor: pointer;
   right: 0.9rem;
   display: none;
 }
 .login-form .input-wrapper .input-field:valid ~ i.eye-icon{
   display: block;
 }
 
 .login-form .Forgot-Pass-Link{
    display: block;
    width: fit-content;
    margin-top: -0.46rem;
 }
 .login-form .login-button{
   width: 100%;
   height: 56px;
   cursor: pointer;
   outline: none;
   border: none;
   color: #fff;
   font-weight: 500;
   font-size: 1.125rem;
   background: #5f41e4;
   margin-top: 2.19rem;
   border-radius: 0.31rem;
   transition: 0.2s ease
 }
 .login-form .login-button:hover{
   background: #4320df;
 }
 .login-container .signup-text{
    text-align: center;
    font-weight: 500;
    margin: 1.75rem 0 0.31rem;
 }
 .login-container a{
    color:#5f41e4 ;
    font-weight: 500;
    text-decoration: none;
 }
 .login-container a:hover{
    text-decoration: underline;
 }